* {
  box-sizing: border-box;
}

#dashboard {
  display: flex;  
  height: 100vh;
  width: 100vw;
  flex-direction: column;
}

#map-display {
  flex: 1;
}

.destination {
  color: #667680;
}

#travel-panel {
  padding: 0.5em;
  flex: 1;
  height: 100vh;
  overflow-y: scroll;
}

#photo-panel {
  padding: 0 1em;
}

#photos {
  overflow-y: scroll;
  height: 300px;
}

.space {
  margin-top: 1em;
}

h1 {
  color: #54AEEF;
  border-bottom: 2px solid #CDD7DE;
  display: inline;
  padding-bottom: 0.3em;
}

@media (min-width: 666px) {

  #dashboard {
    flex-direction: row;
  }

  #map-display {
    flex: 1;
  }

  #travel-panel {
    flex: 1;
    height: 100vh;
    padding: 1em 0.5em;
    overflow-y: hidden;
  }
  
}